import 'bootstrap/dist/css/bootstrap.min.css';
import '../App.css';
import { Button} from 'react-bootstrap'
import {Link } from "react-router-dom"

function Main(){
    return (
      <div className="wrap">
      <header>
        <nav>
          <div className="clock">9:41</div>
          <ul className="nav-items">
            <i class="fa-solid fa-signal"></i>
            <i class="fa-solid fa-wifi"></i>
            <i class="fa-solid fa-battery-full"></i>
          </ul>
        </nav>
      </header>

      <div className="mainwrap">
        <div className='mainTitle'>나의 찰떡 동물 찾기</div>
        <div className='subTitle'>나와 어울리는 동물은?</div>
        <div className="mainImg">
          <img src="images/animal1.png" alt="Dog" />
          <img src="images/animal2.png" alt="Cat" />
          <img src="images/animal3.png" alt="Tiger" />
          <img src="images/animal4.png" alt="Owl" />
          <img src="images/animal5.png" alt="Frog" />
        </div>
        <Link to="/spinner"><Button className='mainButton' variant="primary">시작하기</Button></Link>
        <div className="copyRight">@T_gooni</div>
      </div>
      <footer></footer>
    </div>
    );
}

export default Main;
