import 'bootstrap/dist/css/bootstrap.min.css';
import '../App.css';
import quizData from "./quiz.json"
import { useEffect } from 'react';
import { useNavigate } from "react-router-dom";
import { useRecoilState } from 'recoil';
import cntItem from '../recoil/cntItem.js';

let x_stack = [];

function Quiz() {  
  const [ counter, setCounter ] = useRecoilState(cntItem);
  const navigate = useNavigate();
  let x = getRandomIndex(quizData.length);
  let sum = 0;
  

  function getRandomIndex(length) {  
    let randNum = parseInt(Math.random() * length);

    while (x_stack.includes(randNum)){
      if(x_stack.length == quizData.length)
        break;
      randNum = parseInt(Math.random() * length);
    }
    if(x_stack.length < quizData.length)
      x_stack.push(randNum);
    //console.log(`counter : ${counter}`)
    //console.log(`x_stack : ${x_stack}`)
    return randNum;
  }

  useEffect(()=>{
    counter.forEach(item=>{(sum) += item})
    if(sum == quizData.length )
      navigate("/result");   
  },[x_stack.length,quizData.length,counter]);
  
  return (
    <div className="wrap">
      <header>
        <nav>
          <div className="clock">9:41</div>
          <ul className="nav-items">
            <i class="fa-solid fa-signal"></i>
            <i class="fa-solid fa-wifi"></i>
            <i class="fa-solid fa-battery-full"></i>
          </ul>
        </nav>
      </header>
      <div className="mainwrap">
        <div className='quizTitle'>
          Q. {quizData[x].title}
        </div>
        <div className='quizImg'>
          <img src={`./images/quizImg${x + 1}.png`} alt="quizImg" />
        </div>
        <ul className='quizAnswer'>
          <li onClick={() => { setCounter([counter[0] + 1, counter[1], counter[2], counter[3]]);}}>{quizData[x].answer[0]}{ }</li>
          <li onClick={() => { setCounter([counter[0], counter[1] + 1, counter[2], counter[3]]);}}>{quizData[x].answer[1]}{ }</li>
          <li onClick={() => { setCounter([counter[0], counter[1], counter[2] + 1, counter[3]]);}}>{quizData[x].answer[2]}{ }</li>
          <li onClick={() => { setCounter([counter[0], counter[1], counter[2], counter[3] + 1]);}}>{quizData[x].answer[3]}{ }</li>
        </ul>
      </div>
      <footer></footer>
    </div>
  );
}
export default Quiz;