import 'bootstrap/dist/css/bootstrap.min.css';
import { Button } from 'react-bootstrap'
import '../App.css';
import { useRecoilState } from 'recoil';
import cntItem from '../recoil/cntItem.js';

function Result() {

  return (
    <div className="wrap">
      <header>
        <nav>
          <div className="clock">9:41</div>
          <ul className="nav-items">
            <i class="fa-solid fa-signal"></i>
            <i class="fa-solid fa-wifi"></i>
            <i class="fa-solid fa-battery-full"></i>
          </ul>
        </nav>
      </header>
      <div className="Animal">
        <Animal />
      </div>
      <div className="resultButton">
        <Button className='mainButton1' variant="primary">공유하기 <i class="fa-solid fa-up-right-from-square"></i></Button>
        <Button className='mainButton2' variant="primary"> <i class="fa-solid fa-rotate-right"></i></Button>
      </div>
    </div>
  );
}

function Animal() {
  const [counter, setCounter] = useRecoilState(cntItem);
  let max = counter[0];
  let maxIndex = 0;

  for (let i = 0; i < counter.length; i++) {
    if (counter[i] > max) {
      max = counter[i];
      maxIndex = i;
    }
  }

  if (maxIndex == 0) {
    return (
      <>
        <div className="Animal_img">
          <img src="images/animal1.png" alt="cat" />
        </div>
        <div className="Animal_txt">
          <b>강아지</b> <br /> 강아지들은 정말 멋진 친구들이야! 그들은 항상 사랑이 넘치고 끈끈한 애정을 보여줘. 활발하고 사교적인 성격을 가진 강아지들은 함께 놀기에 딱 좋아. 특히 장난기가 많아서 강아지와의 놀이 시간은 항상 즐거워. 또한 학습을 통해 좋은 습관을 가질 수 있고, 언제나 남을 행복하게 만들어주는 충실한 친구들이야. 그래서 강아지들과 함께하는 시간은 특별한 시간이 되지!
        </div>
      </>
    );
  }
  else if (maxIndex == 1) {
    return (
      <>
        <div className="Animal_img">
          <img src="images/animal2.png" alt="cat" />
        </div>
        <div className="Animal_txt">
          고양이 <br /> 고양이들은 귀여운 작은 동반자들이야! 그들은 보통 독립적인 성격을 가지고 있지만, 그 뒤에는 따뜻한 애정이 숨어있어. 고요하고 조용한 분위기를 선호하며 함께 있으면 마음이 편안해져. 때로는 귀엽게 깜빡이며 친구들과의 조용한 순간을 즐겨. 그래서 고양이와 함께 있는 시간은 정말 특별하고 편안한 시간이 돼.
        </div>
      </>
    );
  }
  else if (maxIndex == 2) {
    return (
      <>
        <div className="Animal_img">
          <img src="images/animal3.png" alt="cat" />
        </div>
        <div className="Animal_txt">
          호랑이 <br /> 호랑이는 왕자 같은 동물로, 우아하고 무섭게 보이지만 사실은 근사한 성격을 가지고 있어. 그들은 자연에서 자유로운 영혼으로서 자신의 영역을 지키기 위해 노력하는 용감한 생명체야. 호랑이들은 가족과의 유대감을 중요시하며, 특히 어린이들을 세심하게 돌봐. 그래서 호랑이는 놀라울 정도로 아름답고 매혹적인 동물 중 하나야.
        </div>
      </>
    );
  }
  else if (maxIndex == 3) {
    return (
      <>
        <div className="Animal_img">
          <img src="images/animal4.png" alt="cat" />
        </div>
        <div className="Animal_txt">
          부엉이 <br /> 부엉이는 밤에 활동적인 귀여운 동물로, 그들의 성격은 매우 독특해. 부엉이는 지혜롭고 조용하고, 관찰자로서 알려져 있어. 그들은 사소한 이야기도 놓치지 않아. 부엉이는 대개 고요한 분위기에서 안정을 찾으며, 남들에게도 차분한 순간을 선사하는 특별한 존재야. 그래서 부엉이와 함께 있는 순간은 평온하고 특별해.
        </div>
      </>
    );
  }
}

export default Result;